import { words } from "../data/words"

const a_letters = [
    '','','','','','','','','','',
    '','','','','','','','','',''
]

const stress_symbols_with_lowers = {
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':'',
    '':''
}

const stress_symbols = ['','','','','','','','','','']

let words_count = words.length

const randint = (min, max) => {
    return Math.round(max - (Math.random() * (max-min)))
}

const word_stress_variables = (word) => {
    let default_word = word
    word = word.toLocaleLowerCase()
    let nWords = []
    let word_by_letters = word.split('')

    let a_is = []
    let good_var = null
    let a_f_index = 1
    word_by_letters.forEach((letter, index) => {
        let isA = stress_symbols_with_lowers[letter]
        if(isA){
            a_is.push(index)
            if(good_var == null && stress_symbols.includes(default_word[index])){
                good_var = a_f_index
            }
            a_f_index++
        }
    })

    a_is.forEach(i=>{
        let c_word = word.split('')
        c_word[i] = stress_symbols_with_lowers[ c_word[i] ]
        nWords.push(c_word.join(''))
    })

    return {variables:nWords, good_variable:good_var}
}

export const getRandomWord = () => {
    let rand_index = randint(0, words_count-1)
    let word = words[rand_index]
    return {...word, ...word_stress_variables(word.word)}
}
